*{
    margin: 0;
    padding: 0;
    font-family: sans-serif;
}
body{
    height:100vh;
    display: flex;
    justify-content:center;
    background-color: #323131;
}
.convert label{
    display: block;
}
.box{
    width: 100%;
    height: 200px;
    background-image: url(https://images.pexels.com/photos/534216/pexels-photo-534216.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=2);
    background-size: 350px;
    background-repeat: no-repeat;
}
.container{
    margin-top: 50px;
    background-color: #fff;
    width: 350px;
    height: 450px;
    align-items: center;
    justify-content: center
}